"use client";

import React, { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Alert, AlertDescription } from "@/components/ui/alert";

interface PhoneNumber {
  id: number;
  phone: string;
  name?: string;
  createdAt: Date;
}

const PhoneTracker: React.FC = () => {
  const [phone, setPhone] = useState("");
  const [name, setName] = useState("");
  const [error, setError] = useState("");
  const [phoneNumbers, setPhoneNumbers] = useState<PhoneNumber[]>([]);

  // Enhanced regex for international phone numbers
  const validatePhone = (input: string): boolean => {
    const regex = /^\+?[1-9]\d{1,14}$/;
    const cleanPhone = input.replace(/[\s\-()]/g, "");
    return regex.test(cleanPhone) && cleanPhone.length >= 7;
  };

  const handleAddPhone = () => {
    setError("");
    
    if (!phone.trim()) {
      setError("Phone number cannot be empty.");
      return;
    }

    const cleanPhone = phone.trim().replace(/[\s\-()]/g, "");
    
    if (!validatePhone(cleanPhone)) {
      setError("Please enter a valid phone number (7-15 digits, optionally starting with +).");
      return;
    }

    // Check for duplicates
    const phoneExists = phoneNumbers.some(p => 
      p.phone.replace(/[\s\-()]/g, "") === cleanPhone
    );
    
    if (phoneExists) {
      setError("This phone number is already being tracked.");
      return;
    }

    const newNumber: PhoneNumber = {
      id: Date.now(),
      phone: phone.trim(),
      name: name.trim() || undefined,
      createdAt: new Date(),
    };

    setPhoneNumbers((prev) => [...prev, newNumber]);
    setPhone("");
    setName("");
  };

  const handleDelete = (id: number) => {
    setPhoneNumbers((prev) => prev.filter((item) => item.id !== id));
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === "Enter") {
      handleAddPhone();
    }
  };

  return (
    <div className="space-y-6">
      {/* Add Phone Number Form */}
      <Card>
        <CardHeader>
          <CardTitle>Add New Phone Number</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="space-y-2">
              <label htmlFor="phone" className="text-sm font-medium text-foreground">
                Phone Number *
              </label>
              <Input
                id="phone"
                type="tel"
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
                onKeyPress={handleKeyPress}
                placeholder="e.g., +1234567890 or 1234567890"
                className="w-full"
              />
            </div>
            <div className="space-y-2">
              <label htmlFor="name" className="text-sm font-medium text-foreground">
                Name (Optional)
              </label>
              <Input
                id="name"
                type="text"
                value={name}
                onChange={(e) => setName(e.target.value)}
                onKeyPress={handleKeyPress}
                placeholder="e.g., John Doe"
                className="w-full"
              />
            </div>
          </div>
          
          {error && (
            <Alert variant="destructive">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}
          
          <Button 
            onClick={handleAddPhone} 
            className="w-full md:w-auto"
            size="lg"
          >
            Add Phone Number
          </Button>
        </CardContent>
      </Card>

      {/* Tracked Phone Numbers */}
      <Card>
        <CardHeader>
          <CardTitle>
            Tracked Phone Numbers ({phoneNumbers.length})
          </CardTitle>
        </CardHeader>
        <CardContent>
          {phoneNumbers.length === 0 ? (
            <div className="text-center py-8">
              <p className="text-muted-foreground text-lg">No phone numbers tracked yet.</p>
              <p className="text-muted-foreground text-sm mt-2">
                Add your first phone number using the form above.
              </p>
            </div>
          ) : (
            <div className="overflow-x-auto">
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Phone Number</TableHead>
                    <TableHead>Name</TableHead>
                    <TableHead>Added On</TableHead>
                    <TableHead className="text-right">Actions</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {phoneNumbers.map((item) => (
                    <TableRow key={item.id}>
                      <TableCell className="font-mono">
                        {item.phone}
                      </TableCell>
                      <TableCell>
                        {item.name || (
                          <span className="text-muted-foreground italic">No name</span>
                        )}
                      </TableCell>
                      <TableCell className="text-muted-foreground">
                        {item.createdAt.toLocaleDateString()} at{" "}
                        {item.createdAt.toLocaleTimeString([], {
                          hour: "2-digit",
                          minute: "2-digit",
                        })}
                      </TableCell>
                      <TableCell className="text-right">
                        <Button
                          onClick={() => handleDelete(item.id)}
                          variant="destructive"
                          size="sm"
                        >
                          Delete
                        </Button>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Statistics */}
      {phoneNumbers.length > 0 && (
        <Card>
          <CardContent className="pt-6">
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4 text-center">
              <div>
                <div className="text-2xl font-bold text-primary">
                  {phoneNumbers.length}
                </div>
                <div className="text-sm text-muted-foreground">
                  Total Numbers
                </div>
              </div>
              <div>
                <div className="text-2xl font-bold text-primary">
                  {phoneNumbers.filter(p => p.name).length}
                </div>
                <div className="text-sm text-muted-foreground">
                  With Names
                </div>
              </div>
              <div>
                <div className="text-2xl font-bold text-primary">
                  {phoneNumbers.filter(p => p.phone.startsWith('+')).length}
                </div>
                <div className="text-sm text-muted-foreground">
                  International
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
};

export default PhoneTracker;
